import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-chip-container';

const ChipContainer = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <div
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </div>
));

ChipContainer.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

ChipContainer.displayName = 'ChipContainer';

export default ChipContainer;
