import React, { useEffect, useState, useRef } from 'react';
import classnames from 'classnames';
import ButtonIcon from '../../button-icon';
import Chip from '../../chip';
import ChipContainer from '../../chip-container';
import ChipText from '../../chip-text';
import Icon from '../../icon';
import './input-chips-example.scss';

const KEY_CODES = {
  ARROW_DOWN: 40,
  ARROW_LEFT: 37,
  ARROW_RIGHT: 39,
  ARROW_UP: 38,
  BACKSPACE: 8,
  DELETE: 46,
  END: 35,
  ENTER: 13,
  HOME: 36,
  TAB: 9
};

const InputChipsExample = () => {
  const ref = useRef();
  const inputRef = useRef();
  const [value, setValue] = useState('');
  const [focusedId, setFocusedId] = useState(-1);
  const [focused, setFocused] = useState(false);
  const [state, setState] = useState({
    items: [
      {
        focused: false,
        id: 'input-chip-group-test-0',
        value: 'Mango'
      },
      {
        focused: false,
        id: 'input-chip-group-test-1',
        value: 'Orange'
      },
      {
        focused: false,
        id: 'input-chip-group-test-2',
        value: 'Apple'
      },
      {
        focused: false,
        id: 'input-chip-group-test-3',
        value: 'Peach'
      }
    ]
  });

  useEffect(() => {
    const el = ref.current.querySelector(`#input-chip-group-test-${focusedId}`);
    if (Boolean(el)) {
      el.focus();
    }
  }, [focusedId]);

  const handleInputClick = e => {
    setFocusedId(-1);
    setState({
      items: [
        ...state.items.map((item, i) => ({
          ...item,
          focused: false
        }))
      ]
    });
  };

  const handleContainerClick = e => {
    if (inputRef && inputRef.current) {
      inputRef.current.focus();
    }
  };

  const handleDelete = (e, id) => {
    const eventType = e.type;
    const isKeydown = e.type === 'keydown';
    const isClick = eventType === 'click';
    const isDeleteKey = isKeydown && [KEY_CODES.DELETE, KEY_CODES.BACKSPACE].includes(e.charCode || e.keyCode);
    const isDownKey = isKeydown && [KEY_CODES.ARROW_DOWN].includes(e.charCode || e.keyCode);
    const isLeftKey = isKeydown && [KEY_CODES.ARROW_LEFT].includes(e.charCode || e.keyCode);
    const isRightKey = isKeydown && [KEY_CODES.ARROW_RIGHT].includes(e.charCode || e.keyCode);
    const isUpKey = isKeydown && [KEY_CODES.ARROW_UP].includes(e.charCode || e.keyCode);
    if (isClick || isDeleteKey || isLeftKey || isRightKey || isUpKey || isDownKey) {
      const items = state.items.map((item, i) => ({
        ...item,
        focused: isClick ? false : i === id - 1
      }));

      if (isClick || isDeleteKey) {
        if (id > -1 && items.length) {
          setState({
            items: [
              ...items.slice(0, id),
              ...items.slice(id + 1)
            ]
          });
          inputRef.current.focus();
          setFocusedId(isClick ? -1 : id - 1);
        }
      }

      if (isLeftKey || isUpKey) {
        e.preventDefault();
        setFocusedId(id > 0 ? (id % state.items.length) - 1 : state.items.length - 1);
      }

      if (isRightKey || isDownKey) {
        e.preventDefault();
        setFocusedId(id < state.items.length - 1 ? (id % state.items.length) + 1 : 0);
      }
    }
  }

  const handleFocus = e => {
    setFocused(true);
    setFocusedId(-1);
  };

  const handleBlur = e => {
    setFocused(false);
  }

  const handleChange = e => {
    setFocusedId(-1);
    setState({
      items: [
        ...state.items.map((item, i) => ({
          ...item,
          focused: false
        }))
      ]
    });
    setValue(e.target.value);
  };

  const handleButtonBlur = e => {
    setFocusedId(-1);
  }

  const handleKeyDown = e => {
    const code = e.charCode || e.keyCode;
    const value = e.target.value;

    switch (code) {
      case KEY_CODES.DELETE:
      case KEY_CODES.BACKSPACE: {
        if (focusedId === -1 && value === '' && state.items.length) {
          setFocusedId(state.items.length - 1);
          setState({
            items: [
              ...state.items.map((item, i) => ({
                ...item,
                focused: i === state.items.length - 1
              })),
            ]
          });
        }

        if (focusedId !== -1 && value === '' && state.items.length) {
          handleDelete(e, focusedId);
        }
        break;
      }

      case KEY_CODES.ENTER: {
        if (value !== '') {
          setState({
            items: [
              ...state.items,
              {
                focused: false,
                id: `input-chip-group-test-${state.items.length}`,
                value
              }
            ]
          });
          setValue('');
        }
        break;
      }

      case KEY_CODES.ARROW_LEFT:
      case KEY_CODES.ARROW_UP: {
        if (value === '' && state.items.length) {
          e.preventDefault();
          const idToFocus = focusedId > 0 ? (focusedId % state.items.length) - 1 : state.items.length - 1;
          setFocusedId(idToFocus);
          setState({
            items: [
              ...state.items.map((item, i) => ({
                ...item,
                focused: i === idToFocus
              })),
            ]
          });
          setValue('');
        }
        break;
      }

      case KEY_CODES.ARROW_RIGHT:
      case KEY_CODES.ARROW_DOWN: {
        if (value === '' && state.items.length) {
          e.preventDefault();
          const idToFocus = focusedId < state.items.length - 1 ? (focusedId % state.items.length) + 1 : 0;
          setFocusedId(idToFocus);
          setState({
            items: [
              ...state.items.map((item, i) => ({
                ...item,
                focused: i === idToFocus
              })),
            ]
          });
          setValue('');
        }
        break;
      }

      default:
        break;
    }
  };

  return (
    <ChipContainer
      className={classnames('vds-chip-type-input', {
        'vds-state--focus': focused
      })}
      onClick={handleContainerClick}
      ref={ref}
    >
      <span role="list">
        {
          state.items.map((item, i) => (
            <Chip
              key={i}
              focused={item.focused}
              role="listitem"
              selected={i === focusedId}
            >
              <ChipText>{item.value}</ChipText>
              <ButtonIcon
                ariaLabel={`press delete or enter to remove ${item.value}`}
                iconType="light-tiny"
                id={`input-chip-group-test-${i}`}
                onBlur={handleButtonBlur}
                onClick={e => handleDelete(e, i)}
                onFocus={e => setFocusedId(i)}
                onKeyDown={e => handleDelete(e, i)}
                tabIndex={focused
                  ? i === state.items.length - 1 ? 0 : -1
                  : focusedId === -1 ? (i === 0 ? 0: -1) : (i === focusedId ? 0 : -1)
                }
              >
                <Icon name="close" resolution="tiny" />
              </ButtonIcon>
            </Chip>
          ))
        }
        </span>
        <input
          className="vds-chip-input"
          id="chips-example-id"
          onBlur={handleBlur}
          onChange={handleChange}
          onClick={handleInputClick}
          onFocus={handleFocus}
          onKeyDown={handleKeyDown}
          ref={inputRef}
          type="text"
          value={value}
        />
    </ChipContainer>
  );
};

export default InputChipsExample;
