import React, { useEffect, useState } from 'react';
import { bool, func, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-chip';
const CSS_STATE = 'vds-state';

const Chip = ({
  children,
  className,
  disabled,
  focused,
  onBlur,
  onChange,
  onFocus,
  readonly,
  selected,
  type,
  ...remainingProps
}) => {
  const [focus, setFocus] = useState(focused);
  const [select, setSelect] = useState(selected);

  useEffect(() => {
    setFocus(focused);
  }, [focused]);

  useEffect(() => {
    setSelect(selected);
  }, [selected]);

  const handleFocus = event => {
    setFocus(true);
    if (typeof onFocus === 'function') {
      onFocus(event);
    }
  };

  const handleBlur = event => {
    setFocus(false);
    if (typeof onBlur === 'function') {
      onBlur(event);
    }
  };

  const handleChange = event => {
    setSelect(event.target.checked);
    if (typeof onChange === 'function') {
      onChange(event);
    }
  };

  return (
    <div
      className={classnames({
        [`${CSS_PREFIX}`]: type === 'default',
        [`${CSS_PREFIX}--filter`]: type === 'filter',
        [`${CSS_STATE}--disabled`]: Boolean(disabled),
        [`${CSS_STATE}--readonly`]: Boolean(readonly),
        [`${CSS_STATE}--selected`]: Boolean(select),
        [`${CSS_STATE}--focus`]: Boolean(focus)
      }, className)}
      onBlur={handleBlur}
      onChange={handleChange}
      onFocus={handleFocus}
      tabIndex={disabled ? -1 : undefined}
      {...remainingProps}
    >
      {children}
    </div>
  );
};

Chip.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * disabled
  */
  disabled: bool,

  /**
  * focused
  */
  focused: bool,

  /**
   * Callback fired when the input element loses focus.
   */
  onBlur: func,

  /**
   * Callback fired when the input element loses focus.
   */
  onChange: func,

  /**
   * Callback fired when the input element is focused on.
   */
  onFocus: func,

  /**
  * Readonly
  */
  readonly: bool,

  /**
  * Selected
  */
  selected: bool,
  /**
   * Type
   */
  type: oneOf(['default', 'filter'])
};

Chip.defaultProps = {
  type: 'default'
};

Chip.displayName = 'Chip';

export default Chip;
