import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-chip-text';

const ChipText = ({ children, className, ...remainingProps }) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

ChipText.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

ChipText.displayName = 'ChipText';

export default ChipText;
